import React,{Component} from 'react';
import {Tab,Tabs,Grid,Cell,Card,CardTitle,CardActions,CardText,Button,CardMenu,IconButton} from 'react-mdl';
class Project extends Component{
	constructor(props){
		super(props)
		this.state={
			activeTab:0
		};
	}
	toggleCategories(){
		if(this.state.activeTab===0){
			return(
		   <div className="projects-grid">
                  {/*Project1 */}
	             <Card shadow={5} style={{minWidth:'450',margin:'auto'}}>
	               <CardTitle style={{color:'#fff',height:'176px',background:'url(data:image/png;base64,) center / cover'}}>Face Recognition Website</CardTitle>
                   <CardText style={{textAlign:"center"}}>
	                  Shubham Khandelwal
	               </CardText>
	               <CardActions border>
	                <Button  colored><a href="https://github.com/shubham-khandelwal18/FaceRecognition">Github</a></Button>
	                <Button  colored><a href="https://shubham-khandelwal18.github.io/FaceRecognition/">Deployed</a></Button>
	                <Button colored>About</Button>
	               </CardActions>
	               <CardMenu style={{color:'#fff'}}>
	                <IconButton name="share"/>
	               </CardMenu>   
                  </Card>
              {/*Project2 */}
	             <Card shadow={5} style={{minWidth:'450',margin:'auto'}}>
	               <CardTitle style={{color:'#fff',height:'176px',background:'url(data:image/png;base64,) center / cover'}}>Amazona e-comm Website</CardTitle>
	               <CardText style={{textAlign:"center"}}>
	                  Shubham Khandelwal
	               </CardText>
	               <CardActions border>
	                <Button  colored><a href="https://github.com/shubham-khandelwal18/react-ecommerce">Github</a></Button>
	                <Button  colored><a href="https://boiling-earth-97799.herokuapp.com/">Deployed</a></Button>
	                <Button colored>About</Button>
	               </CardActions >
	               <CardMenu style={{color:'#fff'}}>
	                <IconButton name="share"/>
	               </CardMenu>   
                  </Card>
              {/*Project3 */}
	             <Card shadow={5} style={{minWidth:'450',margin:'auto'}}>
	               <CardTitle style={{color:'#fff',height:'176px',background:'url(data:image/png;base64,) center / cover'}}>Movie  Search   Mania OMDB</CardTitle>
	               <CardText style={{textAlign:"center"}}>
	                  Shubham Khandelwal
	               </CardText>
	               <CardActions border>
	                <Button  colored><a href="https://github.com/shubham-khandelwal18/Movie-Search">Github</a></Button>
	                <Button  colored><a href="https://shubham-khandelwal18.github.io/Movie-Search/">Deployed</a></Button>
	                <Button colored>About</Button>
	               </CardActions>
	               <CardMenu style={{color:'#fff'}}>
	                <IconButton name="share"/>
	               </CardMenu>   
                  </Card>
             </div>
		    )
		}
		else if(this.state.activeTab===1){
			return(
             <div className="projects-grid">
			 <Card shadow={5} style={{minWidth:'450',margin:'auto'}}>
	               <CardTitle style={{color:'#fff',height:'176px',background:'url(data:image/png;base64,) center / cover'}}>Robofriends Search Pop-Up </CardTitle>
                   <CardText style={{textAlign:"center"}}>
	                  Shubham Khandelwal
	               </CardText>
	               <CardActions border>
	                <Button  colored><a href="https://github.com/shubham-khandelwal18/robofriends">Github</a></Button>
	                <Button  colored><a href="https://shubham-khandelwal18.github.io/robofriends/">Deployed</a></Button>
	                <Button colored>About</Button>
	               </CardActions>
	               <CardMenu style={{color:'#fff'}}>
	                <IconButton name="share"/>
	               </CardMenu>   
                  </Card>
			 </div>
		    )
		}
		else{
			return(
             <div><h1>Sorry Nothing here! Will be added in future</h1></div>
		    )
		}
	}
	render(){
		return(
         <div className="category-tabs">
          <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>Main</Tab>
                    <Tab>React</Tab>
                    <Tab>Javascript</Tab>
           </Tabs>
           <section>
             <Grid>
               <Cell col={12}>
                 <div className="content">{this.toggleCategories()}</div>
               </Cell>
             </Grid>
           </section>  
         </div>
		)
	}
}
export default Project;